import * as React from 'react';

import DeveloperToolsConsoleActionCreator from './../ActionCreators/DeveloperToolsConsoleActionCreator';
import DeveloperToolsConsoleStore from './../Stores/DeveloperToolsConsoleStore';
import ManifestManager from './DeveloperModules/ManifestManager/ManifestManager';
import PerformanceDisplay from './DeveloperModules/PerformanceDisplay/PerformanceDisplay';
import Tabs from './Tabs/Tabs';

import styles from './DeveloperToolsConsole.module.scss';

/**
 * @todo (VSO bug 236724): localize these strings
 * Const variables for the tab keys. Used to help keep things consistent and clean when the number of tabs grows
 */
const manifests: string = 'manifests';
const performance: string = 'performance';

const tabs: { [id: string]: string } = {
  [manifests]: 'Manifests',
  [performance]: 'Performance'
};

export interface IDeveloperToolsConsoleState {
  visible: boolean;
  topPosition: number;
  selectedTabId: string;
}

export default class DeveloperToolsConsole extends React.Component<void, IDeveloperToolsConsoleState> {
  constructor() {
    super();
    this.state = {
      topPosition: DeveloperToolsConsoleStore.height,
      visible: DeveloperToolsConsoleStore.visible,
      selectedTabId: DeveloperToolsConsoleStore.selectedTabId
    };
  }

  public componentDidMount(): void {
    DeveloperToolsConsoleStore.addListener(this._setState);
  }

  public componentWillUnmount(): void {
    DeveloperToolsConsoleStore.removeListener(this._setState);
  }

  public render(): React.ReactElement<{}> {
    if (this.state.visible) {
      let developerModule: React.ReactElement<{}>;
      switch (this.state.selectedTabId) {
        case manifests:
          developerModule = <ManifestManager />;
          break;
        case performance:
          developerModule = <PerformanceDisplay />;
          break;

        default:
          developerModule = <div className={styles.notImplementedModule}>
                              NOT IMPLEMENTED
                            </div>;
          break;
      }

      return  <div  className={styles.container}
                    style={{ height: `${this.state.topPosition * 100}%` }}>
                <div  className={styles.resizeBar}
                      draggable={true}
                      onDrag={this._onDrag}>
                </div>
                <div>
                  <button onClick={(): void => DeveloperToolsConsoleActionCreator.showHideConsole(false) }
                          className={[styles.closeButton, 'ms-Icon--x', 'ms-Icon'].join(' ')} />

                  <Tabs tabs={tabs}
                        selectedId={this.state.selectedTabId}
                        selectionHandler={(id: string): void => DeveloperToolsConsoleActionCreator.selectTab(id)}/>

                  <div className={styles.developerModuleArea}>
                    {developerModule}
                  </div>
                </div>
              </div>;
    } else {
      /* tslint:disable:no-null-keyword */
      return null;
      /* tslint:enable:no-null-keyword */
    }
  }

  private _onDrag: (e: React.DragEvent) => void = (e: React.DragEvent): void => {
    /* tslint:disable:no-any */
    const mouseEvent: React.MouseEvent = (e as any) as React.MouseEvent;
    /* tslint:enable:no-any */

    if (mouseEvent.pageY !== 0) {
      const height: number = (document.documentElement.clientHeight - mouseEvent.pageY) /
                             document.documentElement.clientHeight;
      DeveloperToolsConsoleActionCreator.resizeConsole(height);
    }
  };

  private _setState: () => void = (): void => {
    this.setState({
      topPosition: DeveloperToolsConsoleStore.height,
      visible: DeveloperToolsConsoleStore.visible,
      selectedTabId: DeveloperToolsConsoleStore.selectedTabId
    });
  };
}



