import IAction from '../Actions/IAction';
import BaseStore from './BaseStore';

import DeveloperConsoleResizeAction from './../Actions/DeveloperConsoleResizeAction';
import DeveloperConsoleShowHideAction from './../Actions/DeveloperConsoleShowHideAction';
import SelectTabAction from './../Actions/SelectTabAction';

const DEFAULT_HEIGHT: number = 1 / 4;
const MIN_HEIGHT: number = 1 / 6;
const MAX_HEIGHT: number = 3 / 4;

export class DeveloperToolsConsoleStore extends BaseStore {

  private static _instance: DeveloperToolsConsoleStore;

  public static get instance(): DeveloperToolsConsoleStore {
    if (!this._instance) {
      this._instance = new DeveloperToolsConsoleStore();
    }

    return this._instance;
  }

  public get height(): number {
    return this._height;
  }

  public get visible(): boolean {
    return this._visible;
  }

  public get selectedTabId(): string {
    return this._selectedTabId;
  }

  private _height: number;
  private _visible: boolean;
  private _selectedTabId: string;

  constructor() {
    super();
    this.addProcessActionCallback(this.processAction);

    this._height = DEFAULT_HEIGHT;
    this._visible = false;
    this._selectedTabId = 'manifests';
  }

  /**
   * Processes the action fired
   */
  public processAction(action: IAction): void {
    if (action instanceof DeveloperConsoleResizeAction) {
      if (action.height >= MAX_HEIGHT) {
        this._height = MAX_HEIGHT;
      } else if (action.height <= MIN_HEIGHT) {
        this._height = MIN_HEIGHT;
      } else {
        this._height = action.height;
      }

      this.emitChange();
    } else if (action instanceof DeveloperConsoleShowHideAction) {
      this._visible = action.show;
      this.emitChange();
    } else if (action instanceof SelectTabAction) {
      this._selectedTabId = action.tabId;
      this.emitChange();
    }
  }
}

// instantiate the store so it can start listening to dispatch events
/* tslint:disable:export-name */
export default DeveloperToolsConsoleStore.instance as DeveloperToolsConsoleStore;
/* tslint:enable:export-name */



