import * as React from 'react';

import styles from './PerformanceDisplay.module.scss';
import  PerformanceGraph from './PerformanceGraph/PerformanceGraph';
import PerformanceHeader from './PerformanceHeader/PerformanceHeader';
import PerformanceDisplayStore, {
  IPerfItem
} from '../../../Stores/PerformanceDisplayStore';

export interface IPerformanceDisplayState {
  /**
   * Objects that contain the data points that will be displayed
   */
  perfItems: IPerfItem[];
  /**
   * Integer value representing the total loading time percieved by the user
   */
  eupl: number;
  /**
   * Integer value that gives the time in ms that the page started loading, measured from navigation start
   */
  startTime: number;
  /**
   * If an error has occurred, this will be set to a message detailing the nature of the error
   */
  errorMessage: string;
}

export default class PerformanceDisplay extends React.Component<void, IPerformanceDisplayState> {

  constructor() {
    super();

    this.state = {
      perfItems: PerformanceDisplayStore.performanceData,
      eupl: PerformanceDisplayStore.eupl,
      startTime: PerformanceDisplayStore.startTime,
      errorMessage: PerformanceDisplayStore.errorMessage
    };
  }

  public componentDidMount(): void {
    PerformanceDisplayStore.addListener(this._setState);
  }

  public componentWillUnmount(): void {
    PerformanceDisplayStore.removeListener(this._setState);
  }

  public render(): React.ReactElement<{}> {

    const errorMessage: string = [styles.errorMessage, styles.msFontColorRedDark].join(' ');

    // @todo (VSO bug 236724): localize these strings
    if (this.state.errorMessage) {
      return (
        <div className={ errorMessage }>
          Sorry, something went wrong.
          <br/>Error Message: { this.state.errorMessage }
        </div>
      );
    } else {
      return (
      <div className={ styles.performanceDisplayContainer }>
        <PerformanceHeader perfItems={ this.state.perfItems } overallDuration={ this.state.eupl }/>
        <div className={ styles.graphContainer }>
          <PerformanceGraph
            perfItems={ this.state.perfItems }
            startTime={ this.state.startTime }
            overallDuration={ this.state.eupl }/>
        </div>
      </div>
      );
    }
  }

  private _setState: () => void = (): void => {
    this.setState({
      perfItems: PerformanceDisplayStore.performanceData,
      eupl: PerformanceDisplayStore.eupl,
      startTime: PerformanceDisplayStore.startTime,
      errorMessage: PerformanceDisplayStore.errorMessage
    });
  };
}


