import AppDispatcher from '../Dispatcher/AppDispatcher';

import SelectManifestAction from './../Actions/SelectManifestAction';

/**
 * Contains all actions related to the Manifest Manager
 */
export default class ManifestManagerActionCreator {
  public static selectManifest(selectedManifestId: string): void {
    AppDispatcher.instance.dispatch(new SelectManifestAction(selectedManifestId));
  }
}


