import BaseStore from './BaseStore';

import { IManifestStoreEntry } from '../../ManifestStore';
import ManifestManagerStore from './ManifestManagerStore';
import {
  IClientSideWebPartManifest
} from '../../../../sp-module-interfaces/src/manifestSchemas/IClientSideWebPartManifest';

/**
 * Used for getting perfomance data from the RumOne data
 */
export interface IRumOnePerf {
  /**
   * JSON-formatted string containing data about API calls made
   * Gets parsed and formatted into IAPICall interface declared below
   */
  APICalls: string;
  /**
   * Value in miliseconds corresponding to the start of the page navigation
   */
  W3cNavigationStart: number;
  /**
   * Value in miliseconds corresponding to the end of the server response
   */
  W3cResponseEnd: number;
  /**
   * JSON-formatted string containing data about the user percieved loading time
   */
  EUPLBreakdown: string;
  /**
   * Value in miliseconds corresponding to the length of the user percieved loading time
   */
  EUPL: number;
  /**
   * A string ID of the first control on the page
   * Example: LeftNavComponent
   */
  Control1Id: string;
  /**
   * Value corresponding to the render time of the control in miliseconds
   */
  Control1RenderTime: number;
  Control2Id: string;
  Control2RenderTime: number;
  Control3Id: string;
  Control3RenderTime: number;
  Control4Id: string;
  Control4RenderTime: number;
}

/**
 * Interface to represent API calls obtained from the RumOne data
 */
export interface IAPICall {
  /**
   * String value that gives the duration in miliseconds of the API call
   */
  duration: string;
  /**
   * String that gives an ISO date value for the start time of the API call
   * Example: '2016-08-05T19:09:20.192Z'
   */
  startTime: string;
  /**
   * String that gives an ISO date value for the end time of the API call
   */
  endTime: string;
  /**
   * String that gives the name of the entity that made the API call
   * Example: httpClient
   */
  name: string;
}

/**
 * Interface to represent each performance data point
 */
export interface IPerfItem {
  /**
   * String ID that comes from the value given in the JSON data
   */
  id: string;
  /**
   *  Readable name to be displayed in the table to users
   */
  name: string;
  /**
   *  Start time in miliseconds of the event
   */
  startVal: number;
  /**
   *  Duration of the event in miliseconds.
   */
  duration: number;
  /**
   *  Loading time breakdown for web parts. Will be undefined for items that aren't web parts.
   */
  breakdown: IWebPartBreakdown;
}

/**
 * Breakdown of web part loading time
 */
export interface IWebPartBreakdown {
  /**
   * Array of API call response times
   */
  dataFetch: IPerfTiming [];
  /**
   * Start and duration of the web part initialization time
   */
  init: IPerfTiming;
  /**
   * Start and duration of module loading time
   */
  modulesLoaded: IPerfTiming;
  /**
   * Start and duration of the render time of the web part
   */
  render: IPerfTiming;
}

/**
 * Used for performance data points to indicate when the loading started and how long it lasted.
 */
export interface IPerfTiming {
  /**
   * Time in miliseconds that the event started
   */
  startVal: number;
  /**
   * Time in miliseconds the the event lasted
   */
  duration: number;
}

/**
 * User Percieved Loading breakdown data
 */
export interface IEUPLBreakdown {
  /**
   * Time in miliseconds that the application took to initialize
   */
  appStart: number;
  /**
   * Time in miliseconds it took for the server to respond from the W3cNavigationStart event
   */
  w3cResponseEnd: number;
}

export class PerformanceDisplayStore extends BaseStore {

  private static _instance: PerformanceDisplayStore;

  public static get instance(): PerformanceDisplayStore {
    if (!this._instance) {
      this._instance = new PerformanceDisplayStore();
    }

    return this._instance;
  }

  /**
   * Percieved loading time of the page in miliseconds
   */
  private _eupl: number;

  public get eupl(): number {
    return this._eupl;
  }

  /**
   * Time in miliseconds when the page started loading, from the start of page navigation
   */
  private _startTime: number;

  public get startTime(): number {
    return this._startTime;
  }

  /**
   * If an error is encountered while parsing data, it will be put here so the component can access it
   */
  private _errorMessage: string;

  public get errorMessage(): string {
    return this._errorMessage;
  }

  /**
   * This function parses through performance data and puts together some performance data (IPerfItem) objects for the
   * component to display. If errors are encountered, the _errorMessage variable is set and the function returns an
   * empty array. The component checks for the presence of an error message to determine if something went wrong.
   */
  public get performanceData(): IPerfItem[] {
    // Reset error message so don't think there's an error if there isn't one
    this._errorMessage = undefined;

    const perfItems: IPerfItem[] = [];
    let data: IRumOnePerf;
    /* tslint:disable:no-any */
    data = (window as any).g_spPagesPerformancedata as IRumOnePerf;
    /* tslint:enable:no-any */
    if (!data) {
      this._errorMessage = 'There was a problem accessing the performance data.';
      return [];
    }

    let euplBreakdown: IEUPLBreakdown;
    try {
      euplBreakdown = JSON.parse(data.EUPLBreakdown);
    } catch (error) {
      this._errorMessage = 'An error was caught when parsing the percieved latency data.';
      /* tslint:disable:no-console */
      console.debug(error);
      /* tslint:enable:no-console */
      return [];
    }

    let apiCallsData: IAPICall[];
    try {
      apiCallsData = JSON.parse(data.APICalls);
    } catch (error) {
      this._errorMessage = 'An error was caught when parsing the API data.';
      /* tslint:disable:no-console */
      console.debug(error);
      /* tslint:enable:no-console */
      return [];
    }

    perfItems.push({
      id: 'w3cResponseEnd',
      name: 'Server Response',
      startVal: data.W3cNavigationStart,
      duration: euplBreakdown.w3cResponseEnd,
      breakdown: undefined
    });

    perfItems.push({
      id: 'appStart',
      name: 'Application Initialization',
      startVal: data.W3cResponseEnd,
      duration: euplBreakdown.appStart,
      breakdown: undefined
    });

    perfItems.push({
      id: 'PageComponent',
      name: 'Page Render',
      startVal: euplBreakdown['PageComponent.start'],
      duration: data.Control3RenderTime -
        (euplBreakdown['PageComponent.start'] - data.W3cNavigationStart),
      breakdown: undefined
    });

    perfItems.push({
      id: 'LeftNavComponent',
      name: 'Left Nav Render',
      startVal: euplBreakdown['LeftNavComponent.start'],
      duration: data.Control1RenderTime -
        (euplBreakdown['LeftNavComponent.start'] - data.W3cNavigationStart),
      breakdown: undefined
    });

    perfItems.push({
      id: 'CanvasComponent',
      name: 'Canvas Render',
      startVal: euplBreakdown['CanvasComponent.start'],
      duration: data.Control2RenderTime -
        (euplBreakdown['CanvasComponent.start'] - data.W3cNavigationStart),
      breakdown: undefined
    });

    /**
     * Check if any web parts made API calls, and store them in a data structure per web part
     */
    const apiCalls: { [name: string]: { startVal: number, duration: number }[] } = {};
    const endVals: { [name: string]: number } = {};
    for (const apiCall of apiCallsData) {
      if (apiCall.name.indexOf('WebPart') > -1) {

        if (!apiCalls[apiCall.name]) {
          apiCalls[apiCall.name] = [];
          endVals[apiCall.name] = 0;
        }
        apiCalls[apiCall.name].push({
          startVal: Date.parse(apiCall.startTime),
          duration: +apiCall.duration
        });
        // Find the latest ending API call for total duration calculation later on
        endVals[apiCall.name] = Math.max(
          Date.parse(apiCall.startTime) + +apiCall.duration,
         endVals[apiCall.name]
        );
      }
    }

    /**
     * Get loading breakdown for each web part. Web parts have their loading times broken down further, so an
     * IWebPartBreakdown object is put together for each one.
     */
    for (let webPart: string = data.Control4Id, i: number = 4; webPart; i++, webPart = data['Control' + i + 'Id']) {

      const renderDuration: number = data[`Control${ i }RenderTime`] -
        (euplBreakdown[webPart + '.start'] - data.W3cNavigationStart);

      // Get breakdown of loading data for web parts
      const wpBreakdown: IWebPartBreakdown = {
        dataFetch: apiCalls[webPart] || [],
        init: {
          startVal: euplBreakdown[webPart + '.start'],
          duration: euplBreakdown[webPart + '.init']
        },
        modulesLoaded: {
          startVal: euplBreakdown[webPart + '.start'],
          duration: euplBreakdown[webPart + '.modulesLoaded']
        },
        render: {
          startVal: euplBreakdown[webPart + '.start'],
          duration: renderDuration
        }
      };

      perfItems.push({
        id: webPart,
        name: `${ this._getReadableWebpartName(webPart.replace('Load.', '')) }`,
        startVal: euplBreakdown[webPart + '.start'],
        duration: Math.max(
          renderDuration,
          (endVals[webPart] || 0) - euplBreakdown[webPart + '.start']
        ),
        breakdown: wpBreakdown
      });

    }

    this._startTime = data.W3cNavigationStart;
    this._eupl = data.EUPL;

    return perfItems;
  }

  constructor() {
    super();
    this._getReadableWebpartName = this._getReadableWebpartName.bind(this);
  }

  /**
   * @todo 232311: this scenario works for now but may not in the future.
   * Looks up the web part designated by id and returns the default title as a readable
   * name to display for the graph.
   */
  private _getReadableWebpartName(id: string): string {
    const manifests: { [id: string]: IManifestStoreEntry } = ManifestManagerStore.registeredManifests;

    /**
     * idString is in the format of WebPart.manifestID.instanceID
     * Example: WebPart.eb95c819-ab8f-4689-bd03-0c2d65d47b1f.6fe07da6-45c7-46a2-8f06-77c323e8fc79
     * This block of code strips out the 'WebPart' string, then splits the rest up by the '.' to give an array of the
     * manifest and instance IDs. Then the manifest ID is looked up in the registered manifests, and from that
     * returned entry, the title.default field gives a readable name for the web part.
     */
    let idString: string[] = [];
    idString.push(id.replace('WebPart.', ''));
    idString = idString[0].split('.');

    let manifest: IClientSideWebPartManifest<{}>;

    /**
     * If there is a failure in getting a readable name for the manifest (error getting manifest data, change)
     * in manifest format, etc) just log the error and display the generic name 'web part' so data can still
     * be shown.
     */
    let webPartName: string;
    try {
      manifest = JSON.parse(JSON.stringify(manifests[idString[0]])).manifest;
      webPartName = manifest.preconfiguredEntries[0].title.default;
    } catch (error) {
      this._errorMessage = 'There was an error while getting the web part name from the manifests.';
      console.log(error);
      webPartName = 'Web Part';
    }

    return webPartName;
  }
}

// instantiate the store so it can start listening to dispatch events
/* tslint:disable:export-name */
export default PerformanceDisplayStore.instance as PerformanceDisplayStore;
/* tslint:enable:export-name */


