import * as React from 'react';

import { IManifestStoreEntry } from './../../../../../ManifestStore';

import ManifestListItem from './ManifestListItem/ManifestListItem';

import styles from './ManifestList.module.scss';

export interface IManifestListProps {
  manifests: { [id: string]: IManifestStoreEntry };
  selectedManifestId: string;
  manifestSelectionChangedHandler: (selectedId: string) => void;
}

export default class ManifestList extends React.Component<IManifestListProps, void> {
  public render(): React.ReactElement<IManifestListProps> {
    if (!this.props.manifests || Object.keys(this.props.manifests).length === 0) {
      return  <div>
                No manifests loaded.
              </div>;
    } else {
      return  <ul className={styles.container}>
                {Object.keys(this.props.manifests).map((id: string): React.ReactElement<{}> =>
                  <li key={id}>
                    <ManifestListItem manifest={this.props.manifests[id]}
                                      selected={id === this.props.selectedManifestId}
                                      clickHandler={(): void => this.props.manifestSelectionChangedHandler(id)} />
                  </li>
                )}
              </ul>;
    }
  }
}


