import * as React from 'react';

import ManifestManagerStore from './../../../Stores/ManifestManagerStore';
import { IManifestStoreEntry } from './../../../../ManifestStore';
import ManifestManagerActionCreator from './../../../ActionCreators/ManifestManagerActionCreator';

import ManifestList from './ManifestList/ManifestList';
import ManifestDetails from './ManifestDetails/ManifestDetails';

import styles from './ManifestManager.module.scss';

export interface IManifestManagerState {
  manifests: { [id: string]: IManifestStoreEntry };
  selectedManifestId: string;
}

export default class ManifestManager extends React.Component<{}, IManifestManagerState> {

  constructor() {
    super();
    this.state = {
      manifests: ManifestManagerStore.registeredManifests,
      selectedManifestId: ManifestManagerStore.selectedManifestId
    };
  }

  public componentDidMount(): void {
    ManifestManagerStore.addListener(this._setState);
  }

  public componentWillUnmount(): void {
    ManifestManagerStore.removeListener(this._setState);
  }

  public render(): React.ReactElement<{}> {
    const selectedManifest: IManifestStoreEntry = this.state.manifests[this.state.selectedManifestId];
    const manifestDetailsBox: React.ReactElement<{}> = selectedManifest
      ? <ManifestDetails manifest={selectedManifest} />
      : <div className={styles.noManifestSelectedContainer}>
          No manifest selected
        </div>;

    return  <div className={styles.container}>
              <div className={styles.manifestListContainer}>
                <ManifestList manifests={this.state.manifests}
                              selectedManifestId={this.state.selectedManifestId}
                              manifestSelectionChangedHandler={(selectedManifestId: string): void => {
                                if (selectedManifestId === this.state.selectedManifestId) {
                                  selectedManifestId = undefined;
                                }

                                ManifestManagerActionCreator.selectManifest(selectedManifestId);
                              }} />
              </div>
              <div className={styles.manifestDetailsContainer}>
                {manifestDetailsBox}
              </div>
            </div>;
  }

  private _setState: () => void = (): void => {
    this.setState({
      manifests: ManifestManagerStore.registeredManifests,
      selectedManifestId: ManifestManagerStore.selectedManifestId
    });
  };
}


