import IAction from '../Actions/IAction';
import AppDispatcher from '../Dispatcher/AppDispatcher';

/**
 * Contains common implementation for store in flux data flow model
 */
export default class BaseStore {
  private _listeners: Array<() => void>;
  private _isDisposed: boolean = false;
  private processActionCallback: (action: IAction) => void;
  private _dispatchToken: string;

  /**
   * Initializes a new instance of BaseStore
   */
  constructor() {
    this._listeners = [];
    this._isDisposed = false;
  }

  /** Adds an action callback for everyone action. */
  public addProcessActionCallback(callback: ((action: IAction) => void)): void {
    this.processActionCallback = callback.bind(this);
    this._dispatchToken = AppDispatcher.instance.register((action: IAction) => this.onActionSent(action));
  }

  public get dispatchToken(): string {
      return this._dispatchToken;
  }

  /**
   * Add a listener to this store
   * @param the listener callback
   */
  public addListener(listener: () => void): void {
    if (!this._isDisposed) {
      this._listeners.push(listener);
    }
  }

  /**
   * Removes a listener from the store
   * @param the listener callback
   */
  public removeListener(listener: () => void): void {
    if (!this._isDisposed) {
      const listenerIdx: number = this._listeners.indexOf(listener);
      if (listenerIdx > -1) {
        this._listeners.splice(listenerIdx, 1);
      }
    }
  }

  /**
   * Removes all the registered listeners
   */

  public removeAllListeners(): void {
    this._listeners = [];
  }

  /**
   * Removes a listener from the store
   * @param the listener callback
   *
   * @return the count of listeners tied to this store
   */
  public count(): number {
    return !this._isDisposed ? this._listeners.length : 0;
  }

  /**
   * Invokes all listeners tied to this store
   */
  public emitChange(): void {
    if (!this._isDisposed) {
      this._listeners.forEach((listener: () => void) => listener());
    }
  }

  /**
   * Disposes all listeners
   */
  public dispose(): void {
    if (!this._isDisposed) {
      this._listeners.splice(0);
      this._listeners = undefined;
      this._isDisposed = true;
    }
  }

  private onActionSent(action: IAction): void {
    if (this.processActionCallback) {
      this.tryProcessAction(this.processActionCallback, action);
    }
  }

  private tryProcessAction(processActionCallbackFunction: (action: IAction) => void, action: IAction): void {
    try {
      processActionCallbackFunction(action);
    } catch (error) {
      /* @todo: log. VSO#206341 */

      // Rethrow the error so the ActionCreator can take action to handle error / notify user
      throw error;
    }
  }
}



